:root {
  --font-body: "Sofia Pro", sans-serif;
}

:root {
  --background: #10121c;
  --primary: #ffbc00;
  --secondary: #ff9100;
  --gradiant-end: #ffcc00;
  --gradiant-start: #ffbc00;
  --button-dark: #8694e8;
}

html {
  font-size: 100%;
  box-sizing: border-box;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--background);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 8.75rem;
  max-width: 69.375rem;
  margin: 0 auto;
  position: relative;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.nav {
  display: flex;
  align-items: center;
  color: white;
  padding: 20px 0;
  justify-content: space-between;
  font-size: 20px;
}
.nav__left {
  display: flex;
  align-items: center;
}
.nav__logo {
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-right: 170px;
}
.nav__logo--img {
  height: 30px;
  margin-right: 10px;
}
.nav__items {
  display: flex;
  align-items: center;
  list-style: none;
}
.nav__item {
  margin-right: 50px;
  cursor: pointer;
}
.nav__item:hover {
  color: var(--secondary);
}
.nav__item--active {
  color: var(--secondary);
}
.nav__right {
  display: flex;
  align-items: center;
}

.button {
  padding: 10px 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 35px;
  border-radius: 3px;
}
.button--primary {
  background: linear-gradient(to left, var(--gradiant-start), var(--gradiant-end));
}
.button--secondary {
  background-color: var(--button-dark);
}

.main {
  width: 40%;
  margin-top: 50px;
}
.main__title {
  color: white;
}
.main__title--small {
  font-size: 28px;
  color: var(--primary);
  margin-bottom: 5px;
}
.main__title--main {
  color: white;
  font-size: 46px;
  font-weight: 500;
  margin-bottom: 2px;
}
.main__title--subtitle {
  color: white;
  font-weight: 400;
  margin-bottom: 25px;
}
.main__text {
  font-size: 20px;
  color: white;
  font-weight: 300;
  margin-bottom: 40px;
}
.main__buttons {
  display: flex;
  justify-content: space-around;
}

.phone__img {
  position: absolute;
  width: 60%;
  top: 60px;
  right: 10px;
}
.phone__img img {
  width: 100%;
}